<template>
  <div class="text-center">
    <v-dialog
      v-model="show"
      max-width="1100"
      persistent
    >
      <v-card>
        <v-container
          fluid
          class="minicv-dialog__container"
        >
          <v-row>
            <v-col class="minicv-main">
              <v-col
                class="col-12 minicv-dialog-col__toolbar pr-0 pt-0 pl-0 pb-0"
              >
                <v-btn
                  icon
                  @click.stop="cerrar()"
                >
                  <v-icon
                    large
                    :small="$vuetify.breakpoint.xs"
                  >
                    mdi-close
                  </v-icon>
                </v-btn>
              </v-col>
              <v-col class="col-12 minicv-dialog-col__header">
                <div class="photo">
                  <div class="photo-main">
                    <v-img
                      :lazy-src="
                        `https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto,c_scale/fotos/default.png`
                      "
                      :src="
                        `https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto,c_scale/fotos/${value.terapeuta.Rut}.png`
                      "
                      max-width="400"
                    />
                  </div>
                </div>
                <div class="description">
                  <h2>{{ value.terapeuta.Nombres }}</h2>
                  <div class="info">
                    <div class="cell__titulo-profesional">
                      <div class="cell-title">
                        <h4>Título Profesional:</h4>
                      </div>
                      <div class="cell-text">
                        <span>{{
                          `${value.terapeuta.TituloProfesional}`
                        }}</span>
                      </div>
                    </div>
                    <div class="cell__especializacion">
                      <div class="cell-title">
                        <h4>Especializacion:</h4>
                      </div>
                      <div class="cell-text">
                        <span>{{ `${value.terapeuta.Especializacion}` }}</span>
                      </div>
                    </div>
                  </div>
                </div>
              </v-col>
              <v-col class="col-12 minicv-dialog-col__desempeno">
                <p>
                  <strong>Desempeño Profesional: </strong>
                  <span v-html="value.terapeuta.AreasDeInteres" />
                </p>
              </v-col>
              <v-col class="col-12 minicv-dialog-col__separator">
                <div />
              </v-col>
              <v-col class="col-12 minicv-dialog-col__problemas">
                <div class="problemas-title">
                  <strong>Problemas a tratar: </strong>
                </div>
                <div class="problemas-breakdown">
                  <ul>
                    <li
                      v-for="(item, i) in value.terapeuta.Problemas"
                      :key="i"
                    >
                      {{ item.NombreProblema }}
                    </li>
                  </ul>
                </div>
              </v-col>
              <v-col class="col-12 minicv-dialog-col__separator">
                <div />
              </v-col>
              <v-col class="col-12 minicv-dialog-col__etario">
                <div class="etario-title">
                  <p>
                    <strong>Grupo Etario que Atiende: </strong>
                    {{ value.terapeuta.RangosEtarios }}
                  </p>
                </div>
              </v-col>
              <v-col class="col-12 minicv-dialog-col__footer">
                <v-row>
                  <v-col class="col-12 col-sm-6 text-center text-sm-left">
                    <v-btn
                      href="https://www.centrodeterapia.cl/horas_online"
                      target="_blank"
                      color="success mr-0 btn-reserva"
                      :small="$vuetify.breakpoint.xs"
                    >
                      Reservar Hora
                    </v-btn>
                  </v-col>
                  <v-col class="col-12 col-sm-6 text-center text-sm-right">
                    <v-btn
                      class="warning mr-0 btn-cerrar"
                      :small="$vuetify.breakpoint.xs"
                      @click.stop="cerrar()"
                    >
                      Cerrar
                    </v-btn>
                  </v-col>
                </v-row>
              </v-col>
            </v-col>
          </v-row>
        </v-container>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  export default {
    name: 'MiniCvDialog',
    props: {
      value: {
        active: {
          type: Boolean,
          default: false,
        },
        terapeuta: {
          type: Object,
          default: null,
        },
        sucursal: {
          type: Number,
          default: null,
        },
        default: null,
        type: Object,
      },
    },
    computed: {
      show: {
        get () {
          return this.value.active
        },
        set (value) {
          this.$emit('input', value.active)
        },
      },
    },
    methods: {
      cerrar () {
        this.value.active = false
      },
    },
  }
</script>
